.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  border: 1px solid #2e4057;
  -webkit-transition: 0.4s;
  transition: 0.4s; }

.slider:before {
  position: absolute;
  content: "";
  height: 24px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: black;
  -webkit-transition: 0.4s;
  transition: 0.4s; }

input:checked + .slider {
  background-color: #f1f16f; }

input:focus + .slider {
  box-shadow: 0 0 1px #2e4057; }

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px); }

.slider.round {
  border-radius: 34px;
  background-color: #fff; }

.slider.round:before {
  border-radius: 50%; }

